.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 8px;
  width: 110px;
  color: #f2f2f2;
  box-shadow: 0px 0px 6px 1px #aaa, inset 0px 0px 4px 0px #000;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid black;
  overflow: hidden;
}

.container:active {
  box-shadow: 0px 0px 6px 1px #888;
  filter: brightness(95%);
}

.controlOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.play, .edit {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  opacity: 0;
  transition: .3s;
}

.play {
  height: 80%;
}

.play svg {
  width: 48px;
  fill: rgba(255, 255, 255, 0.8);
}

.edit {
  height: 20%;
  border-top: 1px solid black;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 0px 6px 1px #000;
}

.container:hover .play, .container:hover .edit {
  opacity: 1;
}

.play:hover, .edit:hover {
  background-color: rgba(0, 0, 0, 0.4);
}
