.container {
  width: 200px;
  height: 100vh;
  background-color: #000;
  position: fixed;
  top: 0;
  right: 0;
  transition: transform 1.3s cubic-bezier(0, .52, 0, 1);
  transition: transform .3s ease-in-out;
  overflow: scroll;
  z-index: 1000;

  color: #f2f2f2;
  background: #111;
  display: flex;
  flex-direction: column;
  padding-bottom: 5px;
}

.show {
  transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}

.hide {
  transform: translate3d(100vw, 0, 0);
}

.menuHeader {
  align-self: end;
  padding: 10px;
  cursor: pointer;
}

.menuItemsList {
  list-style-type: none;
  margin: 0;
  padding: 0;
  flex-grow: 1;
}

.menuItem {
  color: #f2f2f2;
  margin: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-right: 1px solid black;
  cursor: pointer;
}

.menuItem span {
  display: block;
  color: #f2f2f2;
  padding: 1em;
  text-decoration: none;
}

.version, .hostname {
  align-self: center;
  font-size: smaller;
  padding: 0.2em;
}
