.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
}

.container > * {
  margin: 0 2px;
}

.container:first-child {
  margin-left: 0;
}
.container:last-child {
  margin-right: 0;
}

.text {
  flex-grow: 1;
}

.editButton, .confirmButton, .cancelButton {
  width: 1.2em;
}

.editIcon {
  height: 16px;
  width: 16px;
  fill: white;
  stroke: none;
}
