.container {
  color: white;
  background-color: black;
  padding: 4px;
  cursor: pointer;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
}

.container:active {
}

.contents {
  flex-grow: 1;
}

.selected {
  background-color: black;
  color: white;
  stroke: white;
}

.deselected {
  background-color: #161616;
  color: #666;
  stroke: #666;
}
