.container {
  background-color: #101010;
  align-self: center;
  align-items: center;
  border-radius: 6px;
  touch-action: none;
  padding: 10px;
}

.boundary {
  box-sizing: border-box;
  cursor: move;
  width: 300px;
  height: 300px;
  overflow: hidden;
  position: relative;
  background: #111;
  border: 1px solid black;
  border-radius: 6px;
  touch-action: none;
}

.viewport {
  width: 110px;
  height: 290px;
  border: 1px solid white;
  position: absolute;
  left: 95px;
  top: 5px;
  box-shadow: rgba(0, 0, 0, 0.2) 0 0 0 2000px;
  touch-action: none;
}

.widthSlider, .heightSlider {
  position: absolute;
  top: 20px;
  height: 250px;
  border-radius: 2px;
  opacity: 0.6;
}

.widthSlider {
  left: 10px;
}

.heightSlider {
  right: 10px;
}

.buttons {
  width: 100%;
  margin: 5px;
  touch-action: none;
}

.buttons .save {
  font-weight: bold;
}

.buttonsRow1, .buttonsRow2 {
  margin-top: 10px;
}

.button {
  flex: 1 0 0;
  margin-left: 5px;
}

.button:first-child {
  margin-right: 5px;
}

.buttonInner {
  padding: 5px;
}

.hiddenInput {
  display: none;
}

.stats {
  font-size: 10px;

}
