.imageScanPosition {
  height: 100%;
}

.wrapper {
  position: relative;
  min-height: 100px;
  min-width: 40px;
  margin: 10px;
  box-shadow: 0px 0px 6px 1px #aaa, inset 0px 0px 4px 0px #000;
  border: 1px solid black;
}

.wrapper:active {
  box-shadow: 0px 0px 6px 1px #888;
  filter: brightness(95%);
}

.left {
  position: absolute;
  width: 100%;
  left: -10px;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 9px solid transparent;
  border-left: 10px solid white;
}

.right {
  position: absolute;
  right: -10px;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 9px solid transparent;
  border-right: 10px solid white;
}

.placeholder, .imageOutput {
  min-width: 40px;
  width: 100%;
  height: 100%;
  cursor: pointer;
  color: #f2f2f2;
}

.imageLoader {
  display: none;
}

.placeholder {
  text-align: center;
  align-self: center;
  padding: 5%;
}
