.container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  color: white;
  background-color: #333;
  padding: 1px;
  border-radius: 6px;
  cursor: pointer;
  text-align: center;
}

.container > *:first-child {
  border-radius: 6px 6px 0 0;
}

.container > *:last-child {
  border-radius: 0 0 6px 6px;
}

.header {
  display: flex;
  flex-direction: row;
  padding: 4px;
  align-items: center;
  flex-grow: 1;
}

.title {
  flex-grow: 1;
}

.selected > * {
  color: green;
  stroke: green;
  background-color: black;
}

.someSelected > * {
  color: yellow;
  stroke: yellow;
  background-color: black;
}

.deselected > * {
  color: #666;
  stroke: #666;
  background-color: #161616;
}
